<template>
  <div>
    <tabs v-model="key" v-if="!$store.state.login.loginStatus">
      <tab-pane label="" name="login">
        <v-touch tag="body" v-on:swipeleft="key='register'">
          <font-awesome-icon icon="user-circle" class="fa-10x login-icon" />
          <Form @submit.native.prevent="loginSubmit('loginForm')" :model="loginForm"
            ref="loginForm">
            <form-input v-for="(item, index) in loginList" :key="index" :detail="item"
              :modelForm="loginForm" @change-input="loginForm=$event"></form-input>
            <FormItem>
              <Button html-type="submit" type="primary" :disabled="$store.state.login.signing"
                long>
                {{$store.state.login.signing?'...':''}}
              </Button>
            </FormItem>
            <FormItem>
              <Button type="info" long>?</Button>
            </FormItem>
          </Form>
        </v-touch>
      </tab-pane>
      <tab-pane label="" name="register">
        <v-touch tag="body" v-on:swiperight="key='login'">
          <Form @submit.native.prevent="registerSubmit('registerForm')" :model="registerForm"
            ref="registerForm">
            <form-input v-for="(item, index) in registerList" :key="index"
              :detail="item" :modelForm="registerForm" @change-input="registerForm=$event"></form-input>
            <FormItem>
              <Button html-type="submit" type="primary" long></Button>
            </FormItem>
          </Form>
        </v-touch>
      </tab-pane>
    </tabs>
    <div v-else>
      <h1> {{$store.state.login.user}}</h1>
      <Button type="primary" @click="logOut()"></Button>
    </div>
  </div>
</template>

<script>
import FormInput from '@/components/common/FormInput.vue';
const registerList = [
  {
    name: '',
    model: 'Referrer',
    placeholder: '',
    type: 'text'
  },
  {
    name: '',
    model: 'Account',
    placeholder: '2 - 15 ',
    type: 'text',
    maxlength: 15,
    pattern: /^[a-zA-Z]\w{1,}/
  },
  {
    name: '',
    model: 'Password',
    placeholder: '6 ',
    type: 'password',
    pattern: /\w{6,}/
  },
  {
    name: '',
    model: 'CheckPsd',
    placeholder: '',
    type: 'password'
  },
  {
    name: '',
    model: 'MoneyPassword',
    placeholder: '',
    type: 'password',
    pattern: /[0-9]/
  },
  {
    name: '',
    model: 'Name',
    placeholder: ' ',
    type: 'text'
  },
  {
    name: '',
    model: 'Mobile',
    placeholder: '',
    type: 'text',
    pattern: /[0-9]/
  },
  {
    name: '',
    model: 'Email',
    placeholder: '',
    type: 'email'
  },
  {
    name: '',
    model: 'Sex',
    placeholder: '',
    type: 'text'
  },
  {
    name: '',
    model: 'Birthday',
    placeholder: '',
    type: 'date'
  },
  {
    name: 'QQ',
    model: 'QQ',
    placeholder: 'QQ',
    type: 'text'
  },
  {
    name: '',
    model: 'IdNumber',
    placeholder: '',
    type: 'text'
  }
];
export default {
  name: 'DrawerLogin',
  components: {
    FormInput
  },
  data: function() {
    return {
      key: 'login',
      masterRule: [],
      loginForm: {
        user: '',
        password: '',
        code: ''
      },
      loginList: [
        {
          name: '',
          model: 'user',
          placeholder: '',
          type: 'text',
          isRequired: true
        },
        {
          name: '',
          model: 'password',
          placeholder: '',
          type: 'password',
          isRequired: true
        },
        {
          name: '',
          model: 'code',
          placeholder: '',
          type: 'text',
          isRequired: true
        }
      ],
      registerForm: {
        Referrer: '',
        Account: '',
        Password: '',
        CheckPsd: '',
        MoneyPassword: '',
        Name: '',
        Mobile: '',
        Email: '',
        Sex: '',
        Birthday: '',
        QQ: '',
        IdNumber: ''
      },
      registerList: []
    };
  },
  methods: {
    loginSubmit: function(name) {
      this.$store.commit('updateSigning');

      this.$refs[name].validate(valid => {
        if (!valid) {
          this.$store.commit('updateSigning');
          return;
        }

        this.axios
          .post(process.env.VUE_APP_LOGIN_URL, {
            Account: this.loginForm.user,
            Password: this.loginForm.password
          })
          .then(
            response => {
              const token = response.ReturnObject;

              this.$store.dispatch('updateLogin', {
                user: this.loginForm.user,
                token: token
              });

              this.$Message.success('!');

              const destinationPage = this.$store.state.login.destinationPage;
              if (destinationPage) {
                this.$router.push(destinationPage);
                this.$store.commit('recordDestinationPage', '');
                this.$store.commit('closeDrawerPage', 'login');
              }

              this.$store.commit('updateSigning');
            },
            () => {
              this.$Message.error('!');
              this.$store.commit('updateSigning');
            }
          );
      });
    },
    registerSubmit: function(name) {
      this.$refs[name].validate(valid => {
        if (!valid) {
          this.$Message.success('!');
          return;
        }

        console.log(this.registerForm);
        this.$Message.success('!');
      });
    },
    logOut: function() {
      this.$store.dispatch('submitLogout');
      this.loginForm = {
        user: '',
        password: '',
        code: ''
      };
      this.$Message.success('!');
    }
  },
  mounted: function() {
    this.axios
      .get('https://next.json-generator.com/api/json/get/V1bIvscJU')
      .then(response => {
        this.masterRule = response.ReturnObject;
        console.log(this.masterRule);
        registerList.forEach(r => {
          r.isRequired =
            this.masterRule['IsRequired_' + r.model] === undefined
              ? true
              : this.masterRule['IsRequired_' + r.model];
          r.isShow =
            this.masterRule['IsShow_' + r.model] === undefined
              ? true
              : this.masterRule['IsShow_' + r.model];
        });
        this.registerList = registerList.filter(r => r.isShow == true);
        console.log(this.registerList);
      });
  }
};
</script>

<style lang="less" scoped>
.login-icon {
  margin: 10px auto;
  width: 100%;
  color: #ccc;
}
</style>
